body{
  background-color: #2196F3;
}

.todostyle{
  display: block;
  font-family: 'Tauri', sans-serif;
  
}
h1{
  padding-top: 14px;
 
  text-decoration: none;
  list-style-type: none;
  word-spacing: 3px;
  background-color:#2196F3 ;

/* align-items: center; */
padding:2px;
margin: 9px;
font-size: 20px;




}
.middle{
padding-top: 60px;
height: 100vh;
margin: 0px 500px;
background-color:white;
text-align: center;
font-size: 30px;
box-shadow: 0px 0px 10px grey;

}
input{
  background-color: white;
  width: 70%;
  height: 40px;
  border: 2px solid white;
  padding-top: 7px;
  padding-bottom: -4px;
  outline: none;
  box-shadow: 0px 2px 0px black;
  
}
.main{
  color: #132C33;
  font-family: 'Nova Square', cursive;
  letter-spacing: 3px;
}
 


.plus{
  background-color: #2ECC71; /* Green */
  border: none;
  color: white;
  padding:5px 13px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 30px;
  margin-left: 15px;
  margin-top: 20px;
  height: 40px;
}
.cross{
  float: right;
  padding-left: 10px;
  padding-right: 9px;
  background-color: red;
  color: white;
  font-family: 'Tauri', sans-serif;
}

